.advertising-link__live {
  background-color: #000000;
  background-image: url("../../img/background/background-header.jpg");
  background-repeat: no-repeat;
  min-height: 400px;
}

.advertising-link__slogan {
  font-weight: 300;
  font-size: 24px;
  line-height: 29px;
  text-align: center;
  color: #ffffff;
}

.advertising-link__list-logo {
  list-style: none;
}

.advertising-link__container {
  background-color: #1D2631;
}

.advertising-link__button-download {
  text-decoration: none;
  text-align: center;
  width: 280px;
  height: 55px;
  border: 3px solid #ffffff;
  border-radius: 29px;
  color: #ffffff;
  font-weight: bold;
  font-size: 18px;
}

.advertising-link__aside {
  color: #ffffff;
  font-size: 14px;
  line-height: 24px;
  text-align: center;
}
