@font-face {
  font-family: "Open Sans";
  font-style: normal;
  font-weight: 400;
  src: url("fonts/opensans.woff2") format("woff2");
  font-display: swap;
}
@font-face {
  font-family: "Open Sans";
  font-style: normal;
  font-weight: 700;
  src: url("../fonts/opensansbold.woff2") format("woff2");
  font-display: swap;
}
@font-face {
  font-family: "Open Sans";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/opensanslight.woff2") format("woff2");
  font-display: swap;
}
@font-face {
  font-family: "Open Sans";
  font-style: normal;
  font-weight: 700;
  src: url("../fonts/opensanslight.woff2") format("woff2");
  font-display: swap;
}
.page-body {
  font-family: "Open Sans", sans-serif;
  font-size: 14px;
  line-height: 24px;
  font-weight: 400;
  font-style: normal;
  background-color: #ffffff;
  width: 320px;
  min-height: 100%;
}

.main-header {
  background-color: #000000;
}

.advertising-link__live {
  background-color: #000000;
  background-image: url("../../img/background/background-header.jpg");
  background-repeat: no-repeat;
  min-height: 400px;
}

.advertising-link__slogan {
  font-weight: 300;
  font-size: 24px;
  line-height: 29px;
  text-align: center;
  color: #ffffff;
}

.advertising-link__list-logo {
  list-style: none;
}

.advertising-link__container {
  background-color: #1D2631;
}

.advertising-link__button-download {
  text-decoration: none;
  text-align: center;
  width: 280px;
  height: 55px;
  border: 3px solid #ffffff;
  border-radius: 29px;
  color: #ffffff;
  font-weight: bold;
  font-size: 18px;
}

.advertising-link__aside {
  color: #ffffff;
  font-size: 14px;
  line-height: 24px;
  text-align: center;
}

.main-nav {
  text-decoration: none;
  color: #ffffff;
}

.site-list__link {
  text-decoration: none;
  color: #ffffff;
  font-size: 18px;
  line-height: 18px;
  font-weight: 400;
}

.advantages__title-page,
.advantages__title-page-2 {
  color: #283645;
  font-weight: 300;
  font-size: 24px;
  line-height: 29px;
  text-align: center;
}

.advantages__wrapper-box {
  position: relative;
  background-color: #F2F2F2;
  clip-path: polygon(0% 3.5%, 100% 0%, 100% 100%, 0 100%);
  min-height: 828px;
}

.advantages__list {
  list-style: none;
  padding-top: 53px;
}

.advantages__wrapper-box::after {
  position: absolute;
  content: "";
  display: block;
  background-image: url("../../img/background/mobile-background.jpg");
  background-repeat: no-repeat;
}