.advantages {
}

.advantages__title-page,
.advantages__title-page-2 {
  color: #283645;
  font-weight: 300;
  font-size: 24px;
  line-height: 29px;
  text-align: center;
}

.advantages__wrapper-box {
  position: relative;
  background-color: #F2F2F2;
  clip-path: polygon(0% 3.5%, 100% 0%, 100% 100%, 0 100%);
  min-height: 828px;
}

.advantages__list {
  list-style: none;
  padding-top: 53px;
}

.advantages__wrapper-box::after {
  position: absolute;
  content: "";
  display: block;
  background-image: url("../../img/background/mobile-background.jpg");
  background-repeat: no-repeat;
}
